import React, { useState, useEffect } from 'react';
import ChangeArticle from './ChangeArticle';

const RecentChanges = () => {
  const [changes, setChanges] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchChanges = async () => {
      try {
        setLoading(true);
        const response = await fetch('/api/bills/?ordering=-updated_at&page_size=3');
        if (!response.ok) throw new Error('   .');
        const data = await response.json();
        setChanges(data.results || []);
      } catch (err) {
        setError(err.message);
        console.error('Error fetching recent changes:', err);
      } finally {
        setLoading(false);
      }
    };
    fetchChanges();
  }, []);

  if (loading) {
    return (
      <section className="py-20 bg-gray-50">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
            <p className="mt-4 text-gray-600">   ...</p>
          </div>
        </div>
      </section>
    );
  }

  if (error) {
    return (
      <section className="py-20 bg-gray-50">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto text-center">
            <p className="text-red-600">{error}</p>
            <button onClick={() => window.location.reload()} className="mt-4 px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"> </button>
          </div>
        </div>
      </section>
    );
  }

  const dataArray = Array.isArray(changes) ? changes : 
                  (changes?.data && Array.isArray(changes.data)) ? changes.data : 
                  (changes?.results && Array.isArray(changes.results)) ? changes.results : [];

  return (
    <section className="py-20 bg-gray-50">
      <div className="container mx-auto px-4">
        <div className="max-w-4xl mx-auto">
          <h2 className="text-4xl font-bold text-gray-900 mb-6">  </h2>
          <p className="text-xl text-gray-600 mb-12">     .</p>
          {dataArray.length === 0 ? (
            <div className="text-center py-12">
              <p className="text-gray-600">   .</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              {dataArray.map((change) => (
                <ChangeArticle
                  key={change.id}
                  title={change.bill_nm}
                  party={change.proposer}
                  description={change.summary || ''}
                  image={change.image || '/default-bill.png'}
                />
              ))}
            </div>
          )}
        </div>
      </div>
    </section>
  );
};

export default RecentChanges;